<template>
  <div>
    <div class="el-header">
      <div class="header_left">
        <i class="el-icon-edit" style="color: white"></i>
        <div class="header_left_text">welcome : {{username}}</div>
      </div>

      <div class="header_right">
        <button class="header_right_text" @click="logout()">logout</button>
        <button class="header_right_text">stats</button>
        <el-tooltip class="item" effect="light" placement="bottom-end" value="true" manual="true">
          <div slot="content">{{ shareurl }}</div>
          <button class="header_right_text">share</button>
        </el-tooltip>
      </div>
    </div>
    <el-table
      :data="tableData"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      ref="multipleTable"
    >
      <el-table-column
        prop="id"
        label="id"
        width="180"
        v-if="tableData.isHidden"
      >
      </el-table-column>
      <el-table-column prop="user" type="selection" label="user" width="180">
      </el-table-column>
      <el-table-column prop="name" label="name"> </el-table-column>
      <el-table-column prop="description" label="description" width="180">
      </el-table-column>
      <el-table-column prop="isComplete" property="isComplete" :formatter="formatter" label="isComplete" width="180">
      </el-table-column>
      <el-table-column prop="isHidden" label="isHidden" :formatter="formatter2"> </el-table-column>
      <el-table-column prop="createdAt" label="createdAt" width="180">
      </el-table-column>
      <el-table-column prop="editedAt" label="editedAt" width="180">
      </el-table-column>
    </el-table>

    <el-button
      type="primary"
      @click="dialogVisible = true"
      style="margin-top: 20px"
      >add</el-button
    >

    <el-button type="primary" @click="del()" style="margin-top: 20px"
      >delete</el-button
    >


  </div>
</template>

  <script>
export default {
  data() {
    return {
      username:"",
      tableData: [
        {
          user: {
            id: 41,
            username: "username3",
            firstName: "newFirst",
            lastName: "newLast",
          },
          id: "001",
          user: "王小虎",
          name: "王小虎",
          description: "任务一",
          isComplete: false,
          isHidden: false,
          createdAt: "王小虎",
          editedAt: "王小虎",
        },
      ],
      shareurl: "",
      dialogVisible: false,
      task: {
        name: "王小虎",
        description: "任务一",
        isComplete: false,
        isHidden: false,
        createdAt: "王小虎",
        editedAt: "王小虎",
      },
    };
  },
  methods: {

    logout() {
      this.$router.push("/");
    },
  },
  mounted: function () {

  },
};
</script>
<style >
.el-header {
  background-image: linear-gradient(to top right, #444, #888, #ddd);
  z-index: 9;
  line-height: 60px;
  height: 60px;
}
.header_left {
  line-height: 60px;
  width: 10%;
  height: 100%;
  text-align: left;
  float: left;
  z-index: 99;
  padding: 0 10px;
}
.header_left_text {
  display: inline-block;
  color: white;
}
.header_right {
  line-height: 60px;
  width: 10%;
  height: 100%;
  text-align: right;
  float: right;
  padding: 0 10px;
}
.header_right_text {
  display: inline-block;
  color: black;
}
</style>


